.large-image {
  height: 500px;
}

.App {
  text-align: center;
  background-color: cornsilk;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Content {
  width: 80%;
  margin-left: 10%;
}

.column {
  float: left;
  width: 30%;
  padding: 5px;
  margin-top: 20px;
  margin-bottom: 20px;
}

/* Clearfix (clear floats) */
.row ::after {
  content: "";
  clear: both;
  display: table;
}

body {
  font-family: Helvetica, sans-serif;
}

h1 {
  color: darkred;
  padding-top: 20px;
}

h2 {
  color: darkred;
}

h3 {
  color: darkred;
  text-align: left;
}

ul {
  list-style-type: none;
}

li {
  padding: 5px;
}

hr {
  margin-top: 40px;
  margin-bottom: 40px;
  border-width: 1px;
  border-style: solid;
  border-color: goldenrod;
  border-bottom: none;
  width: 80%;
}

a {
  color: black;
  text-decoration: none;
}
a:hover {
  color: darkred;
}

p {
  text-align: left;
}

